.login-content {
  --background: #CC5600;


  .login-logo img {
    margin: 15px 0 0 15px;
    max-width: 90%;
    filter: brightness(0) invert(1);
  }

  .form-list {
    background-color: #CC5600;

    .item {
      background-color: #CC5600;

      .label {
        color: #EEE;
        font-size: 24px;
        margin-left: 15px;
      }

      .field-container {
        margin: 5px 25px 15px 15px;

        .input {
          --background: #EEE;
          height: 50px;
          font-size: 26px;
          border-radius: 8px;
          padding-left: 5px;
          width: 100%;

        }
      }
    }
  }

  .login-button {
      margin: 5px 15px 0 10px;
      font-size: 24px;
      border-radius: 10px;
      height: 55px;
      --background: var(--ion-color-tertiary);
  }
}

