
.message {
  width: 100%;
  padding: 14px 18px;
  background-color: white;
  line-height: 1.4;
  text-align: left;

  .top-line {
    font-size: 15px;
    display: flex;
    justify-content: space-between;

    .subject {
      float: left;
      overflow: hidden;
      text-overflow: ellipsis;
      white-space: nowrap;
    }

    .time {
      float: right;
      padding: 0px 4px;
    }
  }

  .bottom-line {
    width: inherit;
    font-size: 14px;
    color: gray;
    text-align: left;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
  }

}

