.orbital-dialog-modal .modal-wrapper {
  height: 30%;
  width: 80%;
  top: 50%;
  padding: 10%;
  position: absolute;
  display: block;

  ion-row {
    height: 60px;

    ion-col {
      .apply-button {
        width: 100%;
        font-size: 20px;
        border-radius: 4px;
        // height: 45px;
        margin: 5px 0;
      }

      ion-button.cancel-btn {
        width: 100%;
        font-size: 20px;
        border-radius: 4px;
        // height: 45px;
        margin: 5px 0;
      }
    }
  }
}
