.request-button {
  margin: 20px 15px 0 10px;
  font-size: 20px;
  border-radius: 5px;
  height: 55px;
  --background: var(--ion-color-secondary);
}

.outer-contentMessage {
  
  .messageIonRow-header {
    padding: 8px 14px 2px;
    display: block;
    font-size: 14px;
    color: #999;

  
    .messageHeaderSubject {
      font-size: 16px;
      color: #000;
      white-space: nowrap;
      display: inline-block;
      margin: 0px;
      padding: 6px 2px;
    }

    .messageHeaderDetails {
      display: inline-block;
      margin: 0px;
      padding: 2px;
    }

    .readCounter {
      text-align: right;
      float: right;
    }
  }

  .messageIonRow-content{
    line-height: 1.4;
    margin: 10px;
    display: block;
  

    .messageBlock {
      display: inline-block;
      padding-top: 14px;
      font-size: 15px;
      border-top: 0.5px solid #ebebeb;
    }
  }

}