//------------------------------------------------------
// Ion Components Overridden
//------------------------------------------------------

ion-toolbar {
  --ion-background-color: #eee;
  --background: #eee;
  border: none;
  --border-width: 0px !important;
}

.header-md::after {
  height: 0px;
}

ion-title.title-default {
  padding: 0px;
  font-size: 20px;
  font-weight: normal;
}

ion-content {
  --ion-background-color: #fff;
  --background: #fff;
  border: 1px solid #ddd;

  .header {
    color: #222;
  }
  .message-list {
    width: 100%;
  }
}

ion-footer {
  .outer-content {
    --ion-background-color: #eee;
    --background: #eee;
  }
}

ion-tab-bar {
  border: unset;
  margin: 4px;
}

.debug-info {
  float: right;
  color: gray;
  font-size: 12px;
  margin-right: 10px;
}

//------------------------------------------------------
// Schedule Page Styles
//------------------------------------------------------
#schedule-page {
  .center {
    margin-left: auto;
    margin-right: auto;
    display: block;
  }

  .filter-not-applied {
    display: none;
  }

  .filter-applied {
    font-size: 84px;
    color: red;
    position: absolute;
    top: -30px;
    right: 3px;
    border-radius: 100%;
  }

  // Add Daily Note Button next to week header event list
  .add-daily-note-button {
    color: #fff;
    background-color: #bbb;
    font-size: 12px;
    padding: 4px 8px;
    border-radius: 4px;
    vertical-align: text-bottom;
    float: right;
    outline: none;
  }
}

//---------------------------------------------------
// Week Picker Control - Schedule Page
//----------------------------------------------------
.chooser {
  background: var(--ion-item-background, var(--ion-background-color, #fff));

  .top {
    margin: 10px 0px;
    display: flex;
    // flex-direction: row;
    align-items: center;
    width: 100%;

    .arrow {
      text-align: center;
      font-size: 25px;
      background-color: #eee;
      outline: none;
    }

    .month-year-container {
      text-align: center;
      flex: 1;
      text-transform: uppercase;

      .datetime-picker-container {
        width: 100%;
        display: block;

        .datetime-picker {
          display: inline-block;
          vertical-align: middle;
        }
      }
    }
  }

  .weekday-list {
    display: flex;
    // flex-direction: row;
    // align-items: stretch;
    width: 100%;
    // height: 50px;
    font-size: 14px;
    margin: 4px auto;

    .weekday-button {
      flex: 1;
      text-align: center;
      padding: 0 10px 0 5px;
      background-color: #eee;
      line-height: 1.4;

      .today {
        border-bottom: 2px solid #ddd;
      }

      .not-today {
        border-bottom: 2px solid #eee;
      }
    }
  }
}

//---------------------------------------------------
// CARDS
//---------------------------------------------------

.card {
  margin: 1px auto;
  padding: 10px;
  font-family: Helvetica, Arial, sans-serif;
  display: flex;
  align-items: center;
  background-color: #fff;
  line-height: 1.4;
  width: 100%;

  .card-first-col {
    min-width: 30px;
    font-size: 14px;
    display: flex;
    align-items: stretch;
    flex-direction: column;

    .day-name {
      // padding: 3px 0 3px 0;
      // padding-bottom: 6px;
    }

    .day-number {
      // padding: 3px 0 3px 0;
    }
  }

  .card-second-col {
    font-size: 14px;
    line-height: 1.4;
    margin-left: 10px;
    text-align: left;
    padding: 0px 10px;
    white-space: pre-line;

    .dept-position-row {
      overflow: hidden;
      white-space: nowrap;
      text-overflow: ellipsis;
    }

    .note-text-row-oneline {
      overflow: hidden;
      white-space: nowrap;
      text-overflow: ellipsis;
    }

    .note-text-row {
      // overflow: inherit;
    }

    .break-div {
      color: #bbbbbb;
      margin: 3px 0 3px 0;
      width: 100%;
    }
  }
}

//--------------------------------------------------
// Daily Notes
//---------------------------------------------------

.daily-note {
  .card-second-col {
    border-left: 7px solid #000;
  }
}

.IonTitle--DailyNote {
  background: #eee;
}

// Edit Daily Note Modal
.edit-daily-modal-content {
  ion-textarea {
    // border: 1px solid gray;
    padding: 5px;
    display: block;
  }

  .character-counter {
    float: right;
    margin-right: 15px;
  }
}

//--------------------------------------------------
// Events
//--------------------------------------------------

.header {
  color: #222;
  padding: 20px 10px 12px;
  font-size: 16px;
}

//---------------------------------------------------
// Edit Event Modal
//---------------------------------------------------

.outer-content {
  font-size: 14px;

  .edit-row {
    height: 40px;
  }

  ion-row {
    align-items: center;

    ion-col {
      .label {
        color: #aaa;
      }
      P .select {
        border: 1px solid #ccc;
        border-radius: 5px;
        padding: 5px;
        margin: 0;
      }

      .textarea {
        border: 1px solid #ccc;
        border-radius: 5px;
      }

      .input-textbox {
        border: 1px solid #ccc;
        border-radius: 5px;
        height: 32px;
      }
    }
  }
}

.visible {
}

.invisible {
  display: none;
}

.footer {
  display: flex;

  .apply-button {
    width: 100%;
    border-radius: 4px;
    // height: 50px;
    font-size: 16px;
    // padding-right: 3px;
    font-weight: 1;
  }

  .delete-button {
    width: 100%;
    border-radius: 4px;
    // height: 50px;
    font-size: 16px;
    // padding-left: 3px;
    font-weight: 1;
    --background: #d9534f;
  }
}

.request-button {
  margin: 20px 15px 0 10px;
  font-size: 16px;
  border-radius: 4px;
  // height: 50px;
}

