
.label {
  color: #AAA;
}


.checkbox {
  padding: 0;
  margin: 5px 0 5px 10px;
  transform: scale(1.5);

}

.select {
  border: 1px solid #CCC;
  border-radius: 5px;
  padding: 5px;
  margin: 0;
}

.textarea {
  border: 1px solid #CCC;
  border-radius: 5px;
}

.error-message {
  height: 26px;
  color: red;
  font-weight: bold;
  font-size: 12px;
}

.usage-message {
  margin: 20px 10px 10px 10px;
  font-size: 24px;
}

button:disabled > div > div.alert-radio-icon {
  visibility: hidden;
}
